import exam from "./FinalExam1";

const Test1 = () => {
  const questions = exam.CurrentPage.PageAssessment.questions;

  let results = [];
  questions.map((question, index) => {
    return question.answers.map((answer, index) => {
      const correct = answer.correct;
      if (correct === true) {
        const textQuestion = question.text;
        const textAnswer = answer.answerValue;
        const result = {
          question: textQuestion,
          answer: textAnswer,
        };
        results.push(result);
      }
      return results;
    });
  });

  return (
    <div style={{ margin: "5rem" }}>
      <h1>Exam 1</h1>
      {results.map((result, index) => {
        return (
          <div key={index}>
            <p>Question: {result.question}</p>
            <p>Answer: {result.answer}</p>
          </div>
        );
      })}
    </div>
  );
};

export default Test1;
